<template>
  <v-container fluid>
    <v-row>
      <v-col>
        <v-card>
          <v-card-title>
            <p class="mb-0 mt-3 mr-15">Книги в эксплуатации</p>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Поиск"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
          <v-data-table
            :headers="headers"
            :search="search"
            :sort-by="['id']"
            :sort-desc="[true, false]"
            :items="filledBooksInUse"
            no-results-text="Нет результатов"
            no-data-text="Нет данных"
            locale="ru-RU"
          >
            <template v-slot:item.actions="props">
              <v-edit-dialog
                :return-value.sync="props.item.actions"
                large
                persistent
                @save="save(props.item.id, props.item.actions)"
                @cancel="cancel"
                @open="open"
              >
                <div>{{ props.item.actions }}</div>
                <div v-if="!props.item.actions">Нет</div>
                <template v-slot:input>
                  <v-select
                    v-model="props.item.actions"
                    :items="itemActions"
                    :label="props.item.actions"
                    solo
                  ></v-select>
                </template>
              </v-edit-dialog>
            </template>
          </v-data-table>
          <v-snackbar v-model="snack" :timeout="3000" :color="snackColor">
            {{ snackText }}
            <template v-slot:action="{ attrs }">
              <v-btn v-bind="attrs" text @click="snack = false">Закрыть</v-btn>
            </template>
          </v-snackbar>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState } from 'vuex'
import moment from 'moment'
import NProgress from 'nprogress'
moment.locale('ru')
export default {
  name: 'BooksInUse',
  // mounted() {
  //   this.timeInterval = setInterval(
  //     () => this.$store.dispatch('book/fetchBooksInUse'),
  //     15000
  //   )
  // },
  // beforeRouteEnter(to, from, next) {
  //   NProgress.start()
  //   store.dispatch('book/fetchBooksInUse').then(() => {
  //     NProgress.done()
  //     next()
  //   })
  // },
  // beforeRouteUpdate(to, from, next) {
  //   store.dispatch('book/fetchBooksInUse').then(() => {
  //     next()
  //   })
  // },
  // beforeDestroy() {
  //   this.timeInterval = null
  //   clearInterval(this.timeInterval)
  // },
  created() {
    NProgress.start()
    this.$store.dispatch('book/fetchBooksInUse')
  },
  computed: {
    ...mapState(['book']),
    filledBooksInUse() {
      return this.book.booksInUse.map(bookInUse => {
        const time = moment().to(bookInUse.time_of_take_away)
        NProgress.done()
        return { ...bookInUse, time }
      })
    }
  },
  methods: {
    save(id) {
      this.snack = true
      this.snackColor = 'success'
      this.snackText = 'Данные сохранены'
      const filteredBookInUse = this.book.booksInUse.filter(bookInUse => {
        return id === bookInUse.id
      })
      filteredBookInUse[0].done = true
      filteredBookInUse[0].time_of_pass = moment().format()
      filteredBookInUse[0].active = false
      this.$store.dispatch('book/updateBookInUse', {
        id: id,
        book: filteredBookInUse[0]
      })
    },
    cancel() {
      this.snack = true
      this.snackColor = 'error'
      this.snackText = 'Отменено'
    },
    open() {
      this.snack = true
      this.snackColor = 'info'
      this.snackText = 'Изменение'
    }
  },
  data() {
    return {
      timeout: null,
      search: '',
      itemActions: ['Да'],
      snack: false,
      snackColor: '',
      snackText: '',
      headers: [
        {
          text: '№',
          align: 'start',
          value: 'id'
        },
        {
          sortable: false,
          text: 'Имя пользователя',
          value: 'user.full_name'
        },
        {
          sortable: false,
          text: 'Название книги',
          value: 'book.title'
        },
        {
          sortable: false,
          text: 'Автор книги',
          value: 'book.author'
        },
        {
          sortable: false,
          text: 'Время',
          value: 'time'
        },
        {
          sortable: false,
          text: 'ISBN',
          value: 'book.isbn'
        },
        {
          text: 'Действия',
          value: 'actions',
          sortable: false
        }
      ]
    }
  }
}
</script>

<style scoped></style>
